import { HomeOutlined, BookOutlined, ContactsOutlined, ContainerOutlined } from '@ant-design/icons-vue';
export function setupAntDesignIcon(app) {
    app.component('HomeOutlined', HomeOutlined);
    app.component('BookOutlined', BookOutlined);
    app.component('ContactsOutlined', ContactsOutlined);
    app.component('ContainerOutlined', ContainerOutlined);
}
