import { Layout, Menu, MenuItem, List, ListItem, Spin, Skeleton, Card, ListItemMeta, Avatar, BackTop, Breadcrumb, Row, Col, PageHeader, Button, Tag, Anchor } from 'ant-design-vue';
import 'ant-design-vue/dist/antd.css';
export function setupAntDesign(app) {
    app.use(Layout);
    app.use(Menu);
    app.use(MenuItem);
    app.use(List);
    app.use(ListItem);
    app.use(Spin);
    app.use(Skeleton);
    app.use(Card);
    app.use(ListItemMeta);
    app.use(Avatar);
    app.use(BackTop);
    app.use(Breadcrumb);
    app.use(Row);
    app.use(Col);
    app.use(PageHeader);
    app.use(Button);
    app.use(Tag);
    app.use(Anchor);
}
