export default [
    {
        path: '/',
        children: [
            {
                path: '',
                name: 'Home',
                component: () => import('@/views/pages/home/HomeIndex.vue'),
                meta: {
                    breadcrumb: [
                        { name: 'Trang chủ' }
                    ],
                    title: 'ADMIN.TITLE'
                }
            },
            {
                path: '/:slug',
                name: 'CourseDetail',
                component: () => import('@/views/pages/course/CourseDetail.vue'),
                meta: {
                    breadcrumb: [
                        { name: 'Trang chủ' },
                        { name: 'Khoá học' }
                    ],
                    title: 'ADMIN.TITLE'
                }
            }
        ]
    }
];
